import * as React from "react";
import {FBFieldWithPriceBase, FBFieldWithPriceBaseState} from "FormBuilder/Fields/Core/FBFieldWithPriceBase";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FBImagePickerModel} from "./FBImagePicker.Model";
import {FBFieldBaseProps} from "FormBuilder/Fields/Core/FBFieldBase";
import {GetModel} from "FormBuilder/Events/GetModel";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBFileModel} from "../FBFile/FBFile.Model";
import {FBFileOptions} from "../FBFile/FBFile.Options";
import {GetFieldOptions} from "FormBuilder/Events/GetFieldOptions";
import {GetField} from "FormBuilder/Events/GetField";
import {FBFile} from "../FBFile/FBFile";
import {FBImagePickerOptions} from "./FBImagePicker.Options";
import './Styles.css';
import {OptionItem} from "FormBuilder/Fields/Options/OptionItem";
import {EventManager} from "shared/core/Events/EventManager";



export class FBImagePicker extends FBFieldWithPriceBase<FBDropDownProps,FBDropDownState>{

    static defaultProps: Partial<FBDropDownProps> = {

    };

    constructor(props: FBDropDownProps) {
        super(props);
    }



    componentWillReceiveProps(nextProps: Readonly<FBDropDownProps>, nextContext: any): void {
        if(this.Model.Parent.IsDesign)
        {
            let selectedOptions=this.props.Model.Options.Options.find(x=>x.Selected);
            this.Model.SelectedValues=selectedOptions?[selectedOptions.Id]:[];


            this.Model.Quantity=this.Model.Options.QuantityDefaultValue;
            this.Model.Refresh();
        }
    }

    SubRender() {
        if(this.Model.Options.ShowQuantitySelector&&this.Model.Options.PriceType!=PriceTypeEnum.quantity)
        {
            return(
            this.Model.Options.QuantityPosition == "bottom" || this.Model.Options.QuantityPosition == "right" ?
                <React.Fragment>
                    {this.GetInput(this.Model.Options.QuantityPosition)}
                    {this.GetQuantityInput()}

                        {this.GetPriceOrRequiredBox()}

                </React.Fragment>:
                <React.Fragment>
                    {this.GetQuantityInput()}
                    {this.GetInput(this.Model.Options.QuantityPosition)}

                        {this.GetPriceOrRequiredBox()}

                </React.Fragment>
            )
        }
        return(
            <React.Fragment>
                {this.GetInput('')}

                    {this.GetPriceOrRequiredBox()}

            </React.Fragment>
        )


    }





    GetQuantityInput(){
        return(
            <div className={'rnTextFieldQuantity '+ this.Model.Options.QuantityPosition}>
                <div className={'rednaoLabel'}>
                    <label style={{fontWeight:'bold'}}>{this.Model.Options.QuantityLabel}</label>
                </div>
                <input className={'rnInputQuantity'} placeholder={this.Model.Options.QuantityPlaceholder} style={{width:'100%'}} type={'text'} value={this.Model.Quantity}
                       onChange={x=>{this.Model.Quantity=this.Model.ParseNumber(x.target.value as any);this.Model.Refresh()}}/>
            </div>

        )
    }

    GetInput(additionalClassNames:string){

        let width:any=this.props.Model.Options.ImageWidth;
        let height:any=this.props.Model.Options.ImageHeight;

        if(width!=null)
        {
            if(width.trim()=='')
                width=undefined;
            else if(!width.endsWith('%'))
            {
                width=parseFloat(width);
                if(isNaN(width)||width==0)
                    width=undefined;
            }
        }


        if(height!=null)
        {
            if(height.trim()=='')
                height=undefined;
            else if(!height.endsWith('%'))
            {
                height=parseFloat(height);
                if(isNaN(height)||height==0)
                    height=undefined;
            }
        }


        return (
            <div className={'rnTextFieldInput '+ additionalClassNames}>
                {this.GetLabel()}
                <div className={'rnImagePickerContainer'}>
                    {this.Model.Options.Options.map(imageItem=>
                        <div className={'rnImagePickerItem '+(this.Model.SelectedValues.some(x=>x==imageItem.Id)?'rnSelected':'')}>
                            <div key={imageItem.Id} className={'rnImagePickerImage'} style={{width:width,height:height}}>
                                <img style={{width:'100%',height:'100%'}} src={imageItem.URL.trim()==''?this.Model.FormBuilder.GetPluginURL()+'images/welcome/logo.png':imageItem.URL} onClick={x=>this.ToggleSelected(imageItem)}/>
                            </div>
                            {imageItem.Label.trim()!=''&&<span>{imageItem.Label.trim()}</span>}
                        </div>
                    )}
                </div>
            </div>

        );
    }




    private OnChange(x: React.ChangeEvent<HTMLSelectElement>) {
        if(x.target.value!='')
            this.Model.RemoveError('required');

        this.Model.SelectedValues=x.target.value.toString()!='-1'?[x.target.value]:[];
        this.Model.Quantity=x.target.value.toString()!='-1'&&this.Model.Quantity==0?1:this.Model.Quantity;
        this.Model.FireValueChanged();
    }

    private ToggleSelected(imageItem: OptionItem) {
        if(!this.Model.Options.AllowMultipleSelection)
            this.Model.SelectedValues=[];
        let selectedItem=this.Model.SelectedValues.find(x=>x==imageItem.Id);
        if(selectedItem===null||typeof selectedItem=="undefined")
            this.Model.SelectedValues.push(imageItem.Id);
        else
            this.Model.SelectedValues.splice(this.Model.SelectedValues.indexOf(selectedItem),1);
        this.Model.FireValueChanged();

    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface FBDropDownProps extends FBFieldBaseProps{
    Model:FBImagePickerModel;

}

interface FBDropDownState extends FBFieldWithPriceBaseState{

}



EventManager.Subscribe(GetModel,(event)=>{
    if(event.Options.Type==FieldTypeEnum.ImagePicker)
        return new FBImagePickerModel(event.Options as FBImagePickerOptions,event.parent).Initialize(event.PreviousData);
});



EventManager.Subscribe(GetFieldOptions,(event)=>{
    if(event.Type==FieldTypeEnum.ImagePicker)
        return new FBImagePickerOptions();
});

EventManager.Subscribe(GetField,(event)=>{
    if(event.Model.Options.Type==FieldTypeEnum.ImagePicker)
        return <FBImagePicker  Model={event.Model as FBImagePickerModel}/>
});

//#endregion "My Region"