
import {FBFieldWithPriceBaseOptions, PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {OptionItem} from "FormBuilder/Fields/Options/OptionItem";
import {StoreDataType} from "shared/core/Store/StoreBase";





export class FBImagePickerOptions extends FBFieldWithPriceBaseOptions{
    public Placeholder:string;
    @StoreDataType(OptionItem)
    public Options:OptionItem[];
    public ImageWidth:string;
    public ImageHeight:string;
    public AllowMultipleSelection:boolean;

    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.ImageWidth='100%';
        this.ImageHeight='';
        this.Options=[];
        this.Type=FieldTypeEnum.ImagePicker;
        this.Label='Image Picker';
        this.Placeholder='';
        this.PriceType=PriceTypeEnum.options;
        this.AllowMultipleSelection=false;

        this.Options=[
            {Id:1, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 1',URL:''} as any,
            {Id:2, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 2',URL:''} as any,
            {Id:3, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 3',URL:''} as any
        ]


    }


}

